@import url("https://fonts.googleapis.com/css2?family=Nunito+Sans:ital,wght@0,300;0,600;0,800;1,300;1,600;1,800&display=swap");
* {
  box-sizing: border-box;
}

body {
  background-color: hsl(207, 26%, 17%);
  color: #fff;
  font-family: "Nunito Sans", sans-serif;
  font-size: 14px;
}

div.headerContents, div.mainArea {
  max-width: 110ch;
  margin: 0 auto;
}

div.header {
  padding: 1rem 0;
  background-color: hsl(209, 23%, 22%);
  color: #fff;
}
div.header h1, div.header p {
  margin: 0;
}
div.header div.headerContents {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

div.search {
  display: flex;
  justify-content: space-between;
  margin: 2rem 0;
  align-items: center;
}

div.serchBox {
  display: flex;
  align-items: center;
  padding: 0.75rem;
  background-color: hsl(209, 23%, 22%);
}
div.serchBox svg {
  width: 16px;
  height: 16px;
  margin-right: 0.75rem;
  fill: #fff;
}
div.serchBox input {
  border: none;
  width: 15rem;
  background-color: rgba(0, 0, 0, 0);
  color: #fff;
}
div.serchBox input::-moz-placeholder {
  color: #fff;
}
div.serchBox input::placeholder {
  color: #fff;
}
div.serchBox input:focus {
  border: 0px solid;
}

div.region {
  padding: 0.75rem;
  background-color: hsl(209, 23%, 22%);
  position: relative;
}
div.region select {
  border: none;
  width: 10rem;
  background-color: rgba(0, 0, 0, 0);
  color: #fff;
}
div.region select option {
  background-color: hsl(209, 23%, 22%);
  margin-top: 1rem;
}

ul.countryListing {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 1rem;
  min-width: 100%;
  padding: 0;
}
ul.countryListing li.countryCard {
  list-style-type: none;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin: 1rem;
  background-color: hsl(209, 23%, 22%);
}
ul.countryListing li.countryCard p {
  padding: 1rem;
  margin: 0;
  font-size: 1.25rem;
}
ul.countryListing li.countryCard ul {
  padding: 1rem;
  padding-top: 0;
}
ul.countryListing li.countryCard ul li {
  list-style-type: none;
}/*# sourceMappingURL=App.css.map */