@import url('https://fonts.googleapis.com/css2?family=Nunito+Sans:ital,wght@0,300;0,600;0,800;1,300;1,600;1,800&display=swap');
// Colors
$lblue : hsl(209, 23%, 22%);
$dblue : hsl(207, 26%, 17%);
$wh : #fff;
*{
  box-sizing: border-box;
}
body{
  background-color: $dblue;
  color:$wh;
  font-family: 'Nunito Sans', sans-serif;
  font-size: 14px;
}
div.headerContents, div.mainArea{
  max-width: 110ch;
  margin: 0 auto;
}

div.header{
  padding: 1rem 0;
  background-color: $lblue;
  color: $wh;
  h1, p{
    margin:0;
  }
  div.headerContents{
    display:flex;
    justify-content: space-between;
    align-items: center;
  }
}

div.search{
  display:flex;
  justify-content: space-between;
  margin: 2rem 0;
  align-items: center;
}

div.serchBox{
  display:flex;
  align-items: center;
  padding: .75rem;
  background-color: $lblue;
  svg{
    width:16px; height: 16px;
    margin-right: .75rem;
    fill:$wh;
  }
  input{
    border:none;
    width: 15rem;
    background-color: rgba(0,0,0,0);
    color:$wh;
    &::placeholder {
      color: $wh;
    }
    &:focus{
      border: 0px solid;
    }
  }
}

div.region{
  padding: .75rem;
  background-color: $lblue;
  position:relative;
  select{
    border:none;
    width: 10rem;
    background-color: rgba(0,0,0,0);
    color:$wh;
    option{
      background-color: $lblue;
      margin-top: 1rem;
    }
  }
}
ul.countryListing{
  display:grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 1rem;
  min-width:100%;
  padding:0;
  li.countryCard{
    list-style-type: none;
    display:flex;
    flex-direction: column;
    justify-content: space-between;
    margin: 1rem;
    background-color: $lblue;
    p{
      padding: 1rem;
      margin: 0;
      font-size: 1.25rem;
    }
    ul{
      padding:1rem;
      padding-top: 0;
      li{
        list-style-type: none;
      }
    }
  }
}